@import url(https://fonts.googleapis.com/css?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  text-align: center;
  background:  rgb(16, 37, 17);
  margin: auto;
}

body {
  text-align: center;
  font-family: 'Special Elite', cursive;
}

.Dark {
  text-align: center;
  background:  rgb(16, 37, 17);
  width: 100vw;
  min-height: 100vh;
}

.Light {
  background:  rgb(231, 240, 231);
}

.App {
  font-family: 'Special Elite', Georgia, 'Times New Roman', Times, serif;
  width: 100vw;
  max-width: 899px;
  color: #4CAF50;
  min-height: 100vh;
  margin-right: auto;
  margin-left: auto;
}

.Pages {
  margin-top: 50px;
  min-height: 70vh;
}

button {
  font-family: 'Special Elite';
  font-size: 19px;
  padding-top: 5px;
  background-color: rgba(247, 255, 250, 0.808);
  -webkit-transition-duration: 0.4s;/*Safari*/
  transition-duration: 0.4s;
  border-radius: 5px;
  border-color: #4CAF50;
  color: #4CAF50;
  letter-spacing: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.082), 0 3px 10px 0 rgba(0, 0, 0, 0.062);
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

div.darkness {
  position: fixed;
  right:    15px;
  bottom:   2px;
}

input {
  margin: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.MenuBlock {
  max-width: 900px;
  width: 100vw;
  height: 8vh;
  max-height: 60px;
  margin: 0px;
  text-align: center;
  cursor: pointer; 
}

.dropdown {
  float: left;
}

.Nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  top: 40px;
  width: 180px;
  background-color: rgba(245, 253, 248, 0.925);
  font-weight:bold;
  position: absolute;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.082), 0 3px 10px 0 rgba(0, 0, 0, 0.062);
  z-index: 8;
}
.dropdown a {
  color: #000;
  text-decoration: none;
}
.menuText li {
  padding: 8px 16px;
  border-bottom: 1px solid #e5e5e5;
}
.menuText li:last-child {
  border-bottom: none;
}
.menuText li:hover {
  background-color: #e5e5e5;
  color: white;
}
.buttonDrop {
  height: 30px;
  float: left;
  font-family: 'Special Elite';
  font-size: 19px;
  max-width: 180px;
  width: 20vw;
  padding-top: 10px;
  background-color: rgba(247, 255, 250, 0.808);
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  border-radius: 1px;
  border-color: #4CAF50;
  color: #000;
  letter-spacing: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.082), 0 3px 10px 0 rgba(0, 0, 0, 0.062);
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  -ms-border-radius: 1px;
  -o-border-radius: 1px;
}

.buttonDrop:hover {
  background-color: #e5e5e5;
  color: white;
}

@media only screen and (max-width: 768px) {
  .buttonDrop{
    height: 6vh;
    width: 33vw;
  }
  .Nav ul {
    top: 7.5vh;
  }
}
