.race {
  height: 70vh;
  background: url(/react_fun/static/media/001-car.c6658762.png) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) no-repeat,
    url(data:image/png;base64,) repeat;

    background-size: auto 15%, 
                    auto 15%, 
                    auto 15%, 
                    auto 20%, 
                    auto 15%, 
                    auto 15%, 
                    auto 15%,
                    auto 32%;
    
    background-position: top 2% left 2%,
                        top 22% left 3%,
                        top 40% left 5%,
                        top 57% left 1%,
                        top 73% left 3%,
                        top 95% left 3%,
                        top 35% left -150%,
                        top 0% left 0%;
    -webkit-transition: background-position 10s ease;
    transition: background-position 10s ease;
    -webkit-transition: all 5s easy-out;
}

.race:hover {
  background-position: top 2% left 80%,
                    top 22% left 60%,
                    top 40% left 100%,
                    top 57% left 45%,
                    top 73% left 72%,
                    top 95% left 83%,
                    top 73% left 380%,
                    top 0% left -300%;
}

.circle {
  width: 30vh;
  height: 30vh;
  background: url(data:image/png;base64,) no-repeat; 
  background-position: 10px 10px;
  background-size: auto 90%;
  border-radius: 50%;
  border-bottom: 120px solid rgb(255, 243, 135);
  border-right: 120px solid rgb(218, 199, 34);
  border-left: 120px solid orange;
  border-top: 120px solid rgb(199, 173, 87);
  margin: auto auto;
  -webkit-transition: all 9000ms cubic-bezier(0.44,-0.37, 1, 1.38);
  transition: all 9000ms cubic-bezier(0.44,-0.37, 1, 1.38);
  z-index: 1;
}

.circle:hover {
  -webkit-transform: rotate(3600deg) scale(0);
          transform: rotate(3600deg) scale(0);
}

.active {
  -webkit-transform: rotate(3600deg);
          transform: rotate(3600deg);
