  border-top-color: 120px solid rgb(255, 243, 135);
  border-left-color: 120px solid rgb(218, 199, 34);
  border-right-color: 120px solid orange; */
}

.wheelBtn {
  margin: 20px -50px;
  z-index: 1;
  width: 100px
}

.dark, .switch, .cirle, .wheelBtn {
  position: absolute;
} 

.dark {
  width: 30vw;
  height: 30vh;
  background-color: transparent;
  z-index: 2;
  border-radius: 50%;
  top: 270px;
  left: 30px;
  -webkit-transition: all 900ms ease;
  transition: all 900ms ease;
}

.switch {
  width: 100px;
  height: 100px;
  border: 50px solid rgba(247, 255, 250, 0.808);
  z-index: 3;
  border-radius: 50%;
  box-sizing: border-box;
  top: 270px;
  left: 30px;
  -webkit-animation: switchOff 3000ms linear infinite alternate;
          animation: switchOff 3000ms linear infinite alternate;
}

.switch:hover {
  -webkit-animation: none;
          animation: none;
}

.switch:hover + .dark {
  background-color: rgb(9, 17, 9);
}

@-webkit-keyframes switchOff {
  from {
    border: 0px solid rgba(247, 255, 250, 0.808);
    opacity: 0;
  }
  to {
    border: 50px solid rgba(247, 255, 250, 0.808);
    opacity: 1;
  }
}

@keyframes switchOff {
  from {
    border: 0px solid rgba(247, 255, 250, 0.808);
    opacity: 0;
  }
  to {
    border: 50px solid rgba(247, 255, 250, 0.808);
    opacity: 1;
  }
}
